<template>
  <div class="accountManageCom">
    <!-- 顶部搜索框 -->
    <div class="top">
        
      <div class="search">
          
        帐号名称：
        <el-input v-model="keywords"></el-input>
        <el-button type="primary" size="small" @click="clickSearch">搜索</el-button>
        
      </div>
    </div>
      <!-- 新增店铺 -->
    <div class="addAccount">
      <el-button type="primary" size="small" @click="clickAddAccount">新增子帐号</el-button>
    </div>
    <!-- 店铺列表 -->
    <div class="bottom">
      <div class="right">
        <!-- 分页 -->
        <div class="fr total">共{{ total }}项</div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[10, 100, 200]"
          :page-size="per_page"
          layout="sizes, prev, pager, next"
          :total="total"
          class="fr"
        ></el-pagination>
      </div>
      <el-table :data="AccountList" style="width: 100%" header-align="center">
        <el-table-column label="id" prop="id" min-width="50" align="center"></el-table-column>
        <el-table-column label="用户名" prop="username" min-width="100" align="center"></el-table-column>
        <el-table-column label="真实姓名" prop="realname" min-width="100" align="center"></el-table-column>
        <el-table-column label="创建时间" prop="create_at" min-width="100" align="center"></el-table-column>
        <el-table-column label="管理店铺" prop="store_names" min-width="200" align="center"></el-table-column>
        <el-table-column label="状态" prop="status" min-width="40" align="center">
           <template slot-scope="scope1">
            <span v-if="scope1.row.status == 1"  class="cologreen">启用中</span>
            <span v-else   class="colored" >禁用中</span>
          </template>
        </el-table-column>
         
        <!-- <el-table-column label="关联账号" prop="store_name" min-width="100" align='center'></el-table-column> -->
        <!-- <el-table-column label="自动同步订单" prop="store_name" min-width="100" align='center'></el-table-column> -->
        <!-- <el-table-column label="授权时间" prop="create_at" min-width="100" align='center'></el-table-column> -->
        <!--  <el-table-column label="状态" prop="status" min-width="50" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status == 1">已授权</span>
            <span v-else>待授权</span>
          </template>
        </el-table-column>-->
        <el-table-column label="操作" min-width="100" align="center">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editAccount(scope.row)">编辑</el-button>
            <el-button
              type="info"
              size="mini"
              @click="forbiddenAccount(scope.row)"
              v-if="scope.row.status == 1"
            >禁用</el-button>
            <el-button
              type="warning"
              size="mini"
              @click="forbiddenAccount(scope.row)"
              v-if="scope.row.status == 2"
            >启用</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 新增店铺对话框 -->
    <el-dialog
      title="新增子帐号"
      :visible.sync="addAccountDialogVisible"
      width="40%"
      class="insertImgDialog"
      @close="addAccountClose"
    >
      <el-form
        class="addAccountForm"
        :model="addAccountForm"
        :rules="addAccountFormRules"
        ref="addAccountFormRef"
      >
        <el-form-item label="用户名：" prop="username">
          <el-input placeholder="支持字母、数字和下划线，4-30字符"  v-model="addAccountForm.username"></el-input>
          
        </el-form-item>
      <br>
        <el-form-item label="密  码：" prop="password">
           <el-input placeholder="长度要在6~16字符之间并且必须包含数字和字母"  show-password v-model="addAccountForm.password"></el-input> 
        </el-form-item>
         <br>
        <el-form-item label="真实姓名：" prop="realname">
          <el-input placeholder="请输入真实姓名" v-model="addAccountForm.realname"></el-input>
        </el-form-item>
         <br>
          <el-form-item label="关联店铺：" prop="checkedShops">
               <el-col :span="18">
                 <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
               <el-checkbox-group v-model="addAccountForm.checkedShops" @change="handleCheckedCitiesChange">
                  <el-checkbox v-for="Shop in ShopList" :label="Shop.id" :key="Shop.id">{{Shop.store_name}}</el-checkbox>
              </el-checkbox-group>
              </el-col>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addAccountDialogVisible = false" size="small">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmAddAccount()"
          size="small"
          style="margin-right: 20px;"
        >保 存</el-button>
      </span>
    </el-dialog>
    <!-- 编辑店铺对话框 -->
    <el-dialog
      title="编辑店铺"
      :visible.sync="editAccountDialogVisible"
      width="40%"
      class="insertImgDialog"
      @close="editAccountClose"
    >
      <el-form
        class="addAccountForm"
        :model="editAccountForm"
        :rules="editAccountFormRules"
        ref="editAccountFormRef"
      >
        <el-form-item label="用户名：" prop="username">
          <el-input placeholder="支持字母、数字和下划线，4-30字符"  :disabled="true" v-model="editAccountForm.username"></el-input>
        </el-form-item>
        <br>
        <el-form-item label="密  码：" prop="password">
           <el-input placeholder="密码为空不修改密码"  show-password v-model="editAccountForm.password"></el-input> 
        </el-form-item>
         <br>
        <el-form-item label="真实姓名：" prop="realname">
          <el-input placeholder="请输入真实姓名" v-model="editAccountForm.realname"></el-input>
        </el-form-item>
         <br>
          <el-form-item label="关联店铺：" prop="checkedShops">
               <el-col :span="18">
                 <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
               <el-checkbox-group v-model="editAccountForm.checkedShops" @change="handleCheckedCitiesChange">
                  <el-checkbox v-for="Shop in ShopList" :label="Shop.id" :key="Shop.id">{{Shop.store_name}}</el-checkbox>
              </el-checkbox-group>
              </el-col>
        </el-form-item>
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAccountDialogVisible = false" size="small">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmEditAccount()"
          size="small"
          style="margin-right: 20px;"
        >保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
        checkAll: false,
        isIndeterminate: false,
       
      addAccountDialogVisible: false, //新增店铺对话框显示隐藏
      editAccountDialogVisible: false, //编辑店铺对话框显示隐藏
      editAccountForm: {
        id: '',
        username: '', //店铺名称
        password: '英文',
        realname: '',
        checkedShops: [] 
      },
      addAccountForm: {
        username: '', //店铺名称
        password: '',
        realname: '',
       checkedShops: []
      },
      addAccountFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 0 到 30 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        realname: [
          { required: true, message: '请输入真实姓名', trigger: 'blur' }
        ] 
      },
      editAccountFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 0 到 30 个字符', trigger: 'blur' }
        ],
        realname: [
          { required: true, message: '请输入真实姓名', trigger: 'blur' }
        ] 
      },
      AccountList: [], //开户站列表
      ShopList: [], //店铺列表
      editLanguage: '', //编辑时归家
      currentPage: 1,
       per_page:10,
      total: 0,
      keywords:'',//搜索关键字
    }
  },
  methods: {
    handleSizeChange(val) {
       // console.log(val);
        this.per_page=val;
        this.currentPage = 1
      this.getAccountList();
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getAccountList()
    },
     handleCheckAllChange(val) {
         if(val){
              for (let index = 0; index < this.ShopList.length; index++) {
                const element = this.ShopList[index]
                if( this.editAccountDialogVisible)
                {
                      this.editAccountForm.checkedShops.push(element.id)
                }
                else{
                    this.addAccountForm.checkedShops.push(element.id)
                }
               
            }
              
         }
         else{
               if( this.editAccountDialogVisible)
                {
                     this.editAccountForm.checkedShops = [];
                }
                else{
                     this.addAccountForm.checkedShops = [];
                }
         }
      },
      handleCheckedCitiesChange(value) {
        //  console.log(value);
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.ShopList.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.ShopList.length;
      },
    // 编辑店铺
    editAccount(data) {
      this.editAccountDialogVisible = true;
      this.editAccountForm.id = data.id;
      this.editAccountForm.username = data.username;
      this.editAccountForm.password ='';
      this.editAccountForm.realname = data.realname;
      this.editAccountForm.checkedShops = data.storeids;
      this.editAccountForm.status = data.status;
    },
    // 监听编辑对话框关闭
    editAccountClose() {
      this.$refs.editAccountFormRef.resetFields()
    },
    // 监听对话框关闭
    addAccountClose() {
      this.$refs.addAccountFormRef.resetFields()
    },
    // 禁用启用店铺确定
    async forbiddenAccount(data) {
      if (data.status == '1') {
        this.$confirm('此操作将禁用该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            const res = await this.api.accountStatus({
              id: data.id,
              status: 2
            })
            if (res.data.code == 200) {
              this.$message({
                type: 'success',
                message: '禁用成功'
              })
              this.getAccountList()
            }
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消禁用'
            })
          })
      } else {
        this.$confirm('此操作将启用该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            const res = await this.api.accountStatus({
              id: data.id, 
              status: 1
            })
            if (res.data.code == 200) {
              this.$message({
                type: 'success',
                message: '启用成功'
              })
              this.getAccountList()
            }
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消启用'
            })
          })
      }
    },
     async accountAdd() {
         const res = await this.api.accountAdd(this.addAccountForm)
                if (res.data.code == 200) {
                    this.$message({
                    message: '添加成功',
                    type: 'success'
                    })
                    this.addAccountDialogVisible = false
                this.getAccountList()
           }        
     },
      async accountEdit() {
         const res = await this.api.accountEdit(this.editAccountForm)
                if (res.data.code == 200) {
                    this.$message({
                    message: '修改成功',
                    type: 'success'
                    })
                    this.editAccountDialogVisible = false
                this.getAccountList()
           }        
     },
    // 新增店铺确定
      confirmAddAccount() {
        this.$refs['addAccountFormRef'].validate((valid) => {
          if (valid) {
             this.accountAdd();
          } else {
          //  console.log('error submit!!');
            return false;
          }
        });
    },
    // 编辑店铺确定
     confirmEditAccount() {
        this.$refs['editAccountFormRef'].validate((valid) => {
          if (valid) {
             this.accountEdit();
          } else {
            return false;
          }
        });
    },
    // 点击新增店铺，显示对话框
    clickAddAccount() {
      this.addAccountDialogVisible = true
    },
    clickSearch() {
      this.currentPage = 1
      this.getShopList()

    },
    // 获取开户站列表
    async getAccountList() {
      const res = await this.api.accountList({ page: this.currentPage,per_page:this.per_page,username: this.keywords })
      if (res.data.code == 200) {
        this.AccountList = res.data.data.data
         this.total = res.data.data.total;
      }
    },
    // 获取店铺列表
    async getShopList() {
      const res = await this.api.storeList({ page: this.currentPage,store_name: this.keywords })
      if (res.data.code == 200) {
        this.ShopList = res.data.data.data;
       // this.total = res.data.data.total
      }
    }
  },
  created() {
    this.getShopList()
    this.getAccountList()
  }
}
</script>
